.preloader {
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  z-index: 9999999;
  text-align: center;
  overflow: hidden;
  transition: 0.4s ease-out;
  background-color: #f4f4f4;
}

.preloader .block .loader-image {
  transition: 0.3s ease-out;
  height: 90px;
  width: 90px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  border-radius: 50%;
  position: relative;
}

.preloader .block .loader-image::after {
  position: absolute;
  content: "";
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  border-radius: 44%;
  border: 1px solid;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    border-color: rgba(255, 49, 88, 0.4);
    transform: rotateZ(0);
  }
  25% {
    border-color: rgba(255, 187, 51, 0.4);
    transform: rotateZ(90deg);
  }
  50% {
    border-color: rgba(255, 49, 88, 0.4);
    transform: rotateZ(180deg);
  }
  75% {
    border-color: rgba(30, 133, 139, 0.4);
    transform: rotateZ(270deg);
  }
  100% {
    border-color: rgba(255, 49, 88, 0.4);
    transform: rotateZ(360deg);
  }
}

.preloader .block .loader-image img {
  height: 30px;
}

.preloader .block .loader-text span {
  display: block;
  transition: 0.3s ease-out;
}

.preloader .block .loader-text span:nth-child(1) {
  font-size: 24px;
  font-weight: 300;
}

.preloader .block .loader-text span:nth-child(2) {
  font-size: 36px;
  font-weight: 700;
}

.preloader.loaded {
  height: 0;
  transition-delay: .8s;
}

.preloader.loaded .block .loader-image {
  opacity: 0;
  transform: translateY(-40px);
}

.preloader.loaded .block .loader-text span:nth-child(1) {
  opacity: 0;
  transform: translateY(-35px) rotateZ(-5deg);
  transition-delay: 0.2s;
}

.preloader.loaded .block .loader-text span:nth-child(2) {
  opacity: 0;
  transform: translateY(-30px) rotateZ(-5deg);
  transition-delay: 0.4s;
}

