.btn {
  font-size: 15px;
  padding: 14px 27px;
  color: #ffffff;
  border-radius: 5px;
  border: 0;
  font-weight: 500;
  transition: .3s;
  position: relative;
}

.btn:active, .btn:focus {
  border: 0;
  box-shadow: none !important;
  border-color: #FF3158 !important;
}

.btn.btn-sm {
  padding: 10px 25px;
}

.btn:hover {
  transform: translateY(-3px);
}

.btn:hover .icon {
  background-color: #f3002e;
}

.btn:hover .icon svg {
  transform: translateX(3px);
}

.btn.has-icon {
  padding: 14px 20px;
  padding-right: 60px;
}

.btn.has-icon .icon {
  position: absolute;
  right: 0;
  top: 0;
  background-color: #f1274d;
  height: 100%;
  width: 42px;
  padding: 0 8px;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  transition: 0.3s;
  overflow: hidden;
}

.btn.has-icon .icon svg {
  height: 100%;
  width: 27px;
  margin-left: -1px;
  transition: 0.3s;
}

.btn.btn-primary {
  background-color: #FF3158 !important;
}

.btn.btn-primary:hover, .btn.btn-primary:active, .btn.btn-primary:focus {
  color: #ffffff;
  background-color: #ff1843 !important;
}

.btn.btn-primary:hover {
  box-shadow: 0px 15px 35px rgba(255, 49, 88, 0.3);
}

.btn.btn-outline-primary {
  color: #222222;
  background-color: transparent;
  border: 0.094rem solid #FF3158;
}

.btn.btn-outline-primary:active, .btn.btn-outline-primary:hover, .btn.btn-outline-primary:focus {
  color: #ffffff;
  background-color: #FF3158 !important;
}

.btn.btn-outline-primary:hover {
  box-shadow: 0px 15px 35px rgba(255, 49, 88, 0.3);
}

.btn.btn-secondary {
  background-color: #9b9b9b !important;
}

.btn.btn-secondary:hover, .btn.btn-secondary:active, .btn.btn-secondary:focus {
  color: #ffffff;
  background-color: #686868 !important;
}

.btn.btn-outline-secondary {
  color: #222222;
  background-color: transparent;
  border: 0.094rem solid #CECECE;
}

.btn.btn-outline-secondary:active, .btn.btn-outline-secondary:hover, .btn.btn-outline-secondary:focus {
  color: #ffffff;
