  transform: rotate(45deg);
}

.navbar-toggler .nav-toggle-icon.active .top {
  stroke-dashoffset: -68px;
}

.navbar-toggler .nav-toggle-icon.active .bottom {
  stroke-dashoffset: -68px;
}

