  margin: 40px 0 30px 0;
  border-radius: 15px;
}

.post-blockquote h3 {
  line-height: 32px;
  position: relative;
  padding-left: 10px;
}

.post-blockquote h3:after, .post-blockquote h3:before {
  position: absolute;
  content: "";
  height: 30px;
  width: 33px;
  background-size: contain;
  background-repeat: no-repeat;
}

