// banner style
.banner {
    padding-top: 75px;
    padding-bottom: 160px;
    overflow-x: hidden;
    @include breakAt(md-device) {
        padding-top: 40px;
    }
    .input-group {
        .form-control, button {
            border-radius: 5px !important;
        }
        @include breakAt(sm-device) {
            .form-control {
                width: 100%;
                margin-bottom: 10px;
            }
            button {
                margin-left: 0 !important;
                height: 50px;
                width: 100%;
            }
        }
    }
    .btn-outline-secondary {
		&:active,
		&:hover,
		&:focus {
            color: $black-600 !important;
            border-color: rgba($color-tertiary, .5) !important;
            background-color: transparent !important;
        }
    }
    .banner-image {
        margin-left: 50px;
        @include breakAt(lg-device) {
            margin-left: 0;
        }
        &.has-shapes {
            .shape-sm {
                left: 42%;
                top: 40px;
                @include breakAt(md-device) {
                    display: none;
                }
            }
            .shape-md {
                right: 7px;
                bottom: 30px;
                @include breakAt(md-device) {
                    display: none;
                }
            }
            .shape-lg {
                left: -10px;
                bottom: 20px;
                @include breakAt(md-device) {
                    display: none;
                }
            }
        }
    }
    .shape-1 {
        top: 15%;
        left: 20px;
    }
    .shape-3 {
        bottom: 150px;
        left: 15px;
    }
    .shape-5 {
        left: 32%;
        bottom: 100px;
    }
    .shape-6 {
        bottom: 130px;
        right: 30px;
    }
    .shape-4 {
        top: 50px;
        right: 20px;
    }
    .shape-8 {
        top: 130px;
        left: 35%;
    }
    .shape-7 {
        top: 80px;
        right: 40%;
    }
}

