// video popup button style
.has-video-popup {
    position: relative;
    .video-play-btn {
        border: 0;
        outline: 0;
        height: 70px;
        width: 70px;
        padding: 2px 0 0 4px;
        position: absolute;
        top: 50%;
        left: 50%;
        color: $white;
        background-color: $color-primary;
        border-radius: 50%;
        transform: translate(-50%, -50%);
        transition: 0.3s;
        -webkit-transition: 0.3s;
        -moz-transition: 0.3s;
        -ms-transition: 0.3s;
        -o-transition: 0.3s;
        box-shadow: 0 0 25px $white;
        @include breakAt(md-device) {
            height: 60px;
            width: 59px;
        }
        @include breakAt(xs-device) {
            height: 45px;
            width: 44px;
            svg {
                height: 1.4em;
                width: 1.4em;
                margin-top: -4px;
            }
        }
        &:hover {
            transform: translate(-50%, -50%) scale(1.2);
        }
        &::after,
        &::before {
            content: "";
            position: absolute;
            border-radius: 50%;
            border: 6px solid rgba($color-primary, .5);
            left: -20px;
            top: -20px;
            right: -20px;
            bottom: -20px;
            opacity: 0;
            backface-visibility: hidden;
            animation: pulse 2s linear infinite;
        }
        &::after {
            animation-delay: .5s;
        }
    }
    @keyframes pulse {
      0%{
        opacity: 0;
        transform: scale(.5);
      }
      50%{
        opacity: 1;
      }
      100%{
        opacity: 0;
        transform: scale(1.2);
      }
    }
}
#videoModal {
    .modal-dialog {
        max-width: 800px;
        margin: 30px auto;
    }
    .modal-body {
        position:relative;
        padding:0px;
    }
    .close {
        position:absolute;
        right: 15px;
        top:-40px;
        z-index:999;
        font-size:2rem;
        font-weight: normal;
        color:#fff;
        opacity:1;
    }
}

// brand carousel
.brand-carousel {
    .slick-list {
        margin-left: -17px;
    }
    .brand-item {
