// how-it-works-tab
.how-it-works-tab {
    .nav-item:first-child {
        .nav-link::after,
        .nav-link::before {
            display: none;
        }
    }
    .nav-link {
        font-weight: 600;
        position: relative;
        padding-top: 0;
        min-width: 180px;
        text-align: center;
        z-index: 1;
        color: $black-300;
        @include breakAt(lg-device) {
            min-width: 160px;
            max-width: 100%;
        }
        &::before,
        & .circle-shape::after,
        & .circle-shape::before {
            position: absolute;
            content: '';
        }
        & .circle-shape {
            position: relative;
            display: block;
            height: 32px;
            width: 32px;
            background-color: $gray;
            border-radius: 50px;
            margin: auto;
            margin-bottom: 10px;
            overflow: hidden;
            z-index: 2;
            &::before {
                background-color: $color-primary;
                height: 32px;
                width: 32px;
                top: 0;
                right: 0;
                border-radius: 50px;
                transition: .2s ease-out;
                transform: scale(0);
            }
            &::after {
                background-color: $white;
                height: 16px;
                width: 16px;
                top: 8px;
                left: calc(50% - 8px);
                z-index: 1;
                border-radius: 50px;
            }
        }
        &::before {
            width: 100%;
            height: 2px;
            background-color: #F7F7F7;
            left: calc(-50% + 16px);
            top: 16px;
            border-radius: 50px;
            transition: .3s;
            z-index: -1;
            @include breakAt(md-device) {
                display: none;
            }
        }
        &.active {
            background-color: transparent;
            color: $color-primary;
            & .circle-shape::before {
                transform: scale(1);
            }
        }
    }

    .tab-pane {
        &.active {
            .anim-to-top {animation: anim-to-top .7s;}
            .anim-to-bottom {animation: anim-to-bottom .7s;}
        }
    }
    @keyframes anim-to-top {
        0% { opacity: 0; transform: translateY(-15px); }
        100% { opacity: 1; transform: translateY(0); }
    }
    @keyframes anim-to-bottom {
        0% { opacity: 0; transform: translateY(15px); }
        100% { opacity: 1; transform: translateY(0); }
    }
}

