// pricing section style
.pricing-switch {
    label {
        cursor: text;
        user-select: none;
        padding: 0 15px;
    }
    .toggle {
        height: 34px;
        cursor: pointer;
        z-index: 1;
        position: relative;
        .switch {
            display: inline-block;
            height: 100%;
            width: 70px;
            border: 1px solid $black-900;
            border-radius: 50px;
            position: relative;
            &::after {
                position: absolute;
                content: "";
                height: 24px;
                width: 24px;
                border-radius: 50px;
                background-color: $color-primary;
                left: 5px;
                top: 4px;
                z-index: 2;
                transition: 0.25s ease-in-out;
            }
        }
        .pricing-check:checked ~ .switch::after {
            left: 57.5%;
        }
        .pricing-check:active ~ .switch::after {
            transform: scale(0.8);
        }
        .pricing-check {
            position: absolute;
            height: 100%;
            width: 100%;
            z-index: 3;
            opacity: 0;
            cursor: pointer;
        }
    }
    @include breakAt(sm-device) {
        text-align: center;
    }
}
.pricing-table {
    td.active {
        position: relative;
        overflow: hidden;
        .recomended {
            position: absolute;
            background: #dca71c;
            color: #ffffff;
            font-weight: 600;
            padding: 0 25px;
            transform: rotate(45deg);
            right: -29px;
            top: 26px;
            font-size: 10px;
            letter-spacing: 1px;
        }
    }

    td, th {
        border: 1px solid #EFEFEF;
        padding: 20px;
        width: 20%;
    }
    td span {
        vertical-align: middle;
    }
    thead td, thead th {
        border-bottom-width: 1px;
        background: $color-primary;
        border-color: #FF5F7D;
    }
    thead td:first-child, thead th:first-child {
        border-top-color: transparent;
        border-left-color: transparent;
        border-top-left-radius: 25px;
    }
    thead td:last-child, thead th:last-child {
        border-top-color: transparent;
        border-right-color: transparent;
        border-top-right-radius: 25px;
    }
    @include breakAt(lg-device) {
        display: block;
        thead, tbody, th, td, tr { 
            display: block;
        }
        thead tr { 
            position: absolute;
            top: -9999px;
            left: -9999px;
        }
        tr { border: 1px solid #ccc; }
        td, th {
            padding: 10px 20px;
            width: initial;
        }
        td {
            border: none;
            border-bottom: 1px solid #eee;
            position: relative;
            padding-left: 50%;
        }
        td:before {
            position: absolute;
            top: 10px;
            left: 20px;
            width: 45%;
            padding-right: 10px;
            white-space: nowrap;
            font-weight: bold;
            content: attr(data-label);
        }
    }
    @include breakAt(sm-device) {
        td {
            padding-left: 40%; 
        }
        td:before {
            width: 39%;
        }
    }
}

